<template>
    <v-container fluid>
        <div class="v-data-table elevation-1 theme--light">
            <div class="v-data-table__wrapper">
                <table>
                    <thead class="v-data-table-header">
                    <td>ID</td>
                    <td>Latitude</td>
                    <td>Longitude</td>
                    <td>Speed</td>
                    <td>Stopped</td>
                    <td>Last Update</td>
                    </thead>
                    <tbody>
                    <tr v-for="drone in drones" :key="drone.id">
                        <td>{{ drone.id }}</td>
                        <td>{{ drone.latitude }}</td>
                        <td>{{ drone.longitude }}</td>
                        <td>{{ drone.speed }}</td>
                        <td>{{ drone.stopped }}</td>
                        <td>{{ drone.lastUpdate }}</td>
                    </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </v-container>
</template>

<script>
  export default {
    name: 'MainComponent',

    data: () => ({
      drones: {}
    }),

    sockets: {
      serverChannel(data) {
        this.drones[data.id] = data;
        // from socket io we have to forceUpdate on vue otherwise the ui is not refreshed
        this.$forceUpdate();
      }
    },
  };
</script>
